import superagent from 'superagent'
// import { getToken } from 'utils/authLocalStorage'
// import formatUrl from 'apis/formatUrl'

const formatUrl = (path) => {
  // const adjustedPath = path[0] !== '/' ? '/' + path : path
  // if (_.startsWith(path, 'http') || _.startsWith(path, '/api')) return path
  // if (process.env.NODE_ENV === 'production') {
  //   if (process.env.APP_ENV === 'staging') return 'https://api-staging.fitonapp.com/api/v1' + adjustedPath
  //   return 'https://api.fitonapp.com/api/v1' + adjustedPath
  // } else {
  //   return 'https://api-staging.fitonapp.com/api/v1' + adjustedPath
  // }
  return 'http://jsonplaceholder.typicode.com' + path
}

const methods = ['get', 'post', 'put', 'patch', 'delete']

export default class ApiClient {
  constructor() {
    // console.log('[LogApiClient]===', 'constructor', '===LOG_TEST_END')
    methods.forEach((method) => {
      // console.log('[LogApiClient]===', method, '===LOG_TEST_END')
      this[method] = (path, { params, body, header } = {}) => {
        const request = superagent[method](formatUrl(path))
        if (params) {
          request.query(params)
        }

        if (header) {
          for (let i in header) {
            request.set(i, header[i])
          }
        }
        // request.set('Authorization', `Bearer ${getToken()}`)

        if (body) {
          request.send(body)
        }

        let _promise = new Promise((resolve) => {
          request.end((err, { body, text } = {}) => {
            if (err) {
              if (body && body.statusCode === 401) {
                return window.location.href = '/login/logIn'
              }
            }
            resolve(body || text || err)
          })
        })

        return _promise
      }
    })
  }
  empty() { }
}
