import React, { useEffect } from 'react'
import styles from './index.module.scss'
import lottie from 'lottie-web'

let testAnimation

const LottieTest = () => {

  useEffect(() => {
    testAnimation = lottie.loadAnimation({
      container: document.getElementById("lottie"),
      renderer: 'svg',
      loop: true,
      autoplay: false,
      path: 'animations/Bull_Flag.json'
    })
  }, [])

  const render = () => {
    return (
      <div className={styles.lottieTest}>
        <div className="lottie" id="lottie" />
        <button className="button" onClick={() => {
          testAnimation.play()
          // testAnimation.goToAndPlay(50, true)
          // testAnimation.playSegments([50, 100], true)
        }}>Start</button>
        <button className="button" onClick={() => {
          testAnimation.stop()
        }}>stop</button>
      </div>
    )
  }
  return render()
}

export default LottieTest
