import React, { useContext } from 'react'
import styles from './index.module.scss'
import { noiseDetect } from 'utils/webAudio'
import { IntegrationContext } from 'contexts/IntegrationContext'

const NoiseDetect = props => {

  const { backgroundColor } = useContext(IntegrationContext)

  const render = () => {
    return (
      <div className={styles.noiseDetect} style={{ backgroundColor: "#" + backgroundColor }}>
        <div id="noiseVolume" className="text">Ambient noise level: 0</div>
        <button id="detectButton" className="button" onClick={() => {
          noiseDetect(volume => {
            const noiseVolume = document.getElementById("noiseVolume")
            noiseVolume.innerHTML = "Ambient noise level: " + volume
            const detectButton = document.getElementById("detectButton")
            detectButton.innerHTML = "Stop"
          }, () => {
            const detectButton = document.getElementById("detectButton")
            detectButton.innerHTML = "Stoping... (About 5s)"
            setTimeout(() => {
              detectButton.innerHTML = "Detect"
              props.history.push("/audiotest")
            }, 5000)
          })
        }}>Detect</button>
      </div>
    )
  }
  return render()
}

export default NoiseDetect
