import React, { useState, useEffect, useRef } from 'react'
import styles from './index.module.scss'
import { resetAudio, pauseAudio } from 'utils/webAudio'
import { testApi } from 'apis/apiService'

const pluseCount = 3
const pluseLength = 300
const pluseSpace = pluseLength * (pluseCount * 2 + 2)
const volumeDefault = -20

const AudioTest = () => {

  const [volume, setVolume] = useState(volumeDefault)
  const [gate, setGate] = useState(0)

  const refVolume = useRef()
  const refFrequency = useRef()
  const refEar = useRef()
  const refIntervalArray = useRef()
  const refTimeoutArray = useRef()

  useEffect(() => {
    setOscillator()
    setPan()
    setGain()
    refVolume.current = volumeDefault
    refFrequency.current = 250
    refEar.current = 0
    refIntervalArray.current = []
    refTimeoutArray.current = []
    window.oscillator.start()
    // test api
    testApi().then(result => {
      console.log('[LogAPI]===', result.resp, '===LOG_TEST_END')
    })

  }, [])

  useEffect(() => {
    if (gate) {
      playPulseFlow()
    } else {
      Array.from(refIntervalArray.current).forEach((interval) => {
        window.clearInterval(interval)
      })
      Array.from(refTimeoutArray.current).forEach((timeout) => {
        window.clearTimeout(timeout)
      })
      refIntervalArray.current = []
      refTimeoutArray.current = []
      pauseAudio()
    }
  }, [gate])

  const setOscillator = () => {
    const frequencySelect = document.getElementById("frequency")
    const newFrequency = frequencySelect.options[frequencySelect.selectedIndex].value
    refFrequency.current = newFrequency
  }

  const setPan = () => {
    const panSelect = document.getElementById("pan")
    const newEar = panSelect.options[panSelect.selectedIndex].value
    refEar.current = newEar
  }

  const setGain = () => {
    const newVolume = document.getElementById("volume").value
    refVolume.current = newVolume
    setVolume(newVolume)
  }

  const playPulseFlow = () => {
    for (let i = 0; i < pluseCount; i++) {
      playPulseAudio(i * 2 * pluseLength)
    }
  }

  const playPulseAudio = (delay) => {
    refTimeoutArray.current.push(setTimeout(() => {
      resetAudio(refFrequency.current, refEar.current, refVolume.current, gate)
      refIntervalArray.current.push(setInterval(() => {
        resetAudio(refFrequency.current, refEar.current, refVolume.current, gate)
      }, pluseSpace))
    }, delay))
    refTimeoutArray.current.push(setTimeout(() => {
      pauseAudio()
      refIntervalArray.current.push(setInterval(() => {
        pauseAudio()
      }, pluseSpace))
    }, delay + pluseLength))
  }

  const render = () => {
    return (
      <div className={styles.audioTest}>
        <h1>Audio Test</h1>
        <div className="content">
          <div>
            <h2>Frequency (hz)</h2>
            <select id="frequency" className="dropdown" onChange={setOscillator}>
              <option value="250">250</option>
              <option value="500">500</option>
              <option value="750">750</option>
              <option value="1000">1000</option>
              <option value="1500">1500</option>
              <option value="2000">2000</option>
              <option value="3000">3000</option>
              <option value="4000">4000</option>
              <option value="6000">6000</option>
              <option value="8000">8000</option>
            </select>
          </div>
          <div>
            <h2>Ear selector</h2>
            <select id="pan" className="dropdown" onChange={setPan}>
              <option value="0">Both</option>
              <option value="-1">Left</option>
              <option value="1">Right</option>
            </select>
          </div>
          <div>
            <h2>Volume: {volume} dbFS</h2>
            <input id="volume" type="range" min="-96" max="0" value={volume} step="1" className="slider" onChange={setGain} />
            <div className="volume-buttons">
              <button className="button" onClick={() => {
                let newVolume = volume
                if (volume > -96) {
                  newVolume = +volume - 5
                }
                if (newVolume < -96) {
                  setVolume(-96)
                } else {
                  setVolume(newVolume)
                }
                refVolume.current = newVolume
              }}>-5</button>
              <button className="button" onClick={() => {
                let newVolume = volume
                if (volume < 0) {
                  newVolume = +volume + 5
                }
                if (newVolume > 0) {
                  setVolume(0)
                } else {
                  setVolume(newVolume)
                }
                refVolume.current = newVolume
              }}>+5</button>
            </div>
          </div>
          <div>
            <button id="button" className="button" onClick={() => {
              window.scAudio.resume()
              setGate(gate === 0 ? 1 : 0)
            }}>{gate === 0 ? "Play" : "Stop"}</button>
          </div>
        </div>
      </div>
    )
  }

  return render()
}

export default AudioTest
